<template>
  <div class="indexSet-content-parent">
    <!--左边菜单-->
    <div class="goods-menu">
      <div class="goods-menu-top">
        <img
          src="../../assets/img/ico-menu1.png"
          style="margin: 0px 0px 0px 15px;cursor: pointer"
          id="ico-menu1"
          tag="0&quot;"
        />编码族管理
      </div>
      <div class="goods-menu-content">
        <ul id="file" class="filetree treeview">
          <li class="last">
            <span class="folder">商品编码</span>
          </li>
        </ul>
        <div id="goods-code-manager"></div>
      </div>
    </div>
    <!--右边内容-->
    <div class="goods-content">
      <div class="goods-content-top">
        <div class="goods-content-top-search">
          <div class="top-search-pic">
            <input type="text" style="z-index:200" class="top-search-pic-input"/>
          </div>
        </div>
        <div class="goods-content-top-right">
          <div class="goods-content-top-right-pic">
            <router-link to="/kp-set">
              <div class="close-text-exit"></div>
            </router-link>
            <div class="close-notice1" onclick="notice()"></div>
            <div class="close-notice1" onclick="notice()"></div>
            <!--打印-->
            <div class="close-notice2" onclick="notice()"></div>
            <div class="close-text-delete" @click="delete_goods()" id="goods_delete" data-id></div>
            <!-- 修改的按钮 -->
            <div class="close-text-change" @click="change_goods" id="goods_change" data-id></div>
            <!-- 增加的按钮 -->
            <div class="close-text-add" @click="add_goods"></div>
          </div>
        </div>
      </div>
      <div class="goods-content-table">
        <table cellpadding="0px" cellspacing="0px" class="goods-table">
          <thead>
          <tr>
            <th>编码</th>
            <th>名称</th>
            <th>简码</th>
            <th>商品税目</th>
            <th>税率</th>
            <th>规格型号</th>
            <th>计量单位</th>
            <th>单价</th>
            <th>含税价标志</th>
            <th>税收分类编码</th>
            <th>享受优惠政策</th>
            <th>税收分类名称</th>
            <th>优惠政策类型</th>
            <th>隐藏标志</th>
          </tr>
          </thead>
          <tbody id="goods-table1">
          <tr
            v-for="(item, i) in goods_table"
            :key="i"
            @click="select_goods(i)"
            :class="i===goodsNumber?'goods-active':''"
          >
            <td>{{item.CCP_bm_code}}</td>
            <td>{{item.CCP_sp_name}}</td>
            <td>{{item.CCP_Simple_code}}</td>
            <td>{{item.CCP_Commoditytax}}</td>
            <td>{{item.CCP_taxrate}}</td>
            <td>{{item.CCP_Specificationmodel}}</td>
            <td>{{item.CCP_UnitOfMeasurement}}</td>
            <td>{{item.CCP_UnitPrice}}</td>
            <td>{{item.CCP_TaxPriceMark}}</td>
            <td>{{item.CCP_TaxClassificationCode}}</td>
            <td></td>
            <td>{{item.CCP_NameOfTaxClassification}}</td>
            <td></td>
            <td>{{item.CCP_HiddenFlag}}</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

    <!-- 这是添加商品的那个弹窗 -->
    <div id="add-goods-content" style="display:none">
      <div class="goods-save" @click="add_goods_click"></div>
      <!--保存-->
      <div class="goods-cancel" @click="cancel_click"></div>
      <!--取消-->
      <div class="goods-first">
        <!--上级编码、客户名称-->
        <div class="goods-up-code">
          <!--上级编码：最大长度为3，只能输入数字-->
          <input type="text" maxlength="3" readonly class="pop-input" id="CCP_sj_code"/>
        </div>
        <div class="goods-customer-name">
          <input id="CCP_sp_name" type="text" class="pop-input" v-model="goods_form.CCP_sp_name"/>
        </div>
      </div>
      <div class="goods-second">
        <!--编码、商品税目-->
        <div class="goods-code">
          <!--编码：6位,只能输入数字-->
          <input
            type="number"
            class="pop-input"
            maxlength="3"
            id="CCP_bm_code"
            v-model="goods_form.CCP_bm_code"
          />
        </div>
        <div class="goods-goods-code">
          <!--商品税目：4位；只能是数字-->
          <input
            id="CCP_Commoditytax"
            type="text"
            class="pop-input"
            maxlength="4"
            onkeyup="this.value=this.value.replace(/\D/g,'')"
            onafterpaste="this.value=this.value.replace(/\D/g,'')"
            v-model="goods_form.CCP_Commoditytax"
          />
        </div>
      </div>
      <div class="goods-third">
        <!--简码，税率-->
        <div class="goods-shot-code">
          <!--简码：长度6位；只能输入字母和数字；首字母大写-->
          <input
            type="text"
            maxlength="6"
            class="pop-input"
            id="CCP_Simple_code"
            style="text-transform: uppercase"
            onkeyup="value=value.replace(/[^\w\.\/]/ig,'')"
            v-model="goods_form.CCP_Simple_code"
          />
        </div>
        <div class="goods-rate">
          <select id="CCP_taxrate" style="width: 185px" v-model="goods_form.CCP_taxrate">
            <option>3%</option>
            <option>5%</option>
            <option>6%</option>
            <option>9%</option>
            <option>10%</option>
            <option>13%</option>
            <option>16%</option>
            <option>减按1.5%计算</option>
            <option>中外合作油气田</option>
          </select>
        </div>
      </div>
      <div class="goods-forth">
        <!--规格型号，单价-->
        <div class="goods-model">
          <input
            id="CCP_Specificationmodel"
            type="text"
            class="pop-input"
            v-model="goods_form.CCP_Specificationmodel"
          />
        </div>
        <div class="goods-price">
          <!--单价：只能输入数字-->
          <input
            id="CCP_UnitPrice"
            type="text"
            class="pop-input"
            onkeyup="this.value=this.value.replace(/\D/g,'')"
            onafterpaste="this.value=this.value.replace(/\D/g,'')"
            v-model="goods_form.CCP_UnitPrice"
          />
        </div>
      </div>
      <div class="goods-fifth">
        <!--计量单位，含税价标志，隐藏标志-->
        <div class="goods-unit">
          <!--计量单位：限制为4位-->
          <input
            id="CCP_UnitOfMeasurement"
            type="text"
            maxlength="4"
            class="pop-input"
            v-model="goods_form.CCP_UnitOfMeasurement"
          />
        </div>
        <div class="goods_is_tax">
          <select id="CCP_TaxPriceMark" v-model="goods_form.CCP_TaxPriceMark">
            <option value="是">是</option>
            <option value="否">否</option>
          </select>
        </div>
        <div class="goods_is_hidden">
          <select id="CCP_HiddenFlag" v-model="goods_form.CCP_HiddenFlag">
            <option value="是">是</option>
            <option value="否">否</option>
          </select>
        </div>
      </div>
      <div class="goods-sixth">
        <!--税收分类编码，税收分类名称-->
        <div class="goods-classify-code">
          <input
            type="text"
            class="pop-input"
            id="CCP_TaxClassificationCode"
            v-model="goods_form.CCP_TaxClassificationCode"
          />
          <img
            src="../../assets/img/ico_chose.png"
            onclick="pop_classify_code()"
            style="position: absolute;left: 162px;top: 3px;cursor: pointer"
          />
        </div>
        <div class="goods-classify-name">
          <input
            type="text"
            class="pop-input"
            id="CCP_NameOfTaxClassification"
            v-model="goods_form.CCP_NameOfTaxClassification"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        formName: 'goods_form',
        goods_form: {
          // CCP_Commoditytax: '', // 税目
          // CCP_bm_code: '', //编码
          // CCP_Simple_code: '', //简码
          // CCP_taxrate: '', //税率
          // CCP_Specificationmodel: '',	//规格型号
          // CCP_UnitPrice: '',			//单价
          // CCP_UnitOfMeasurement: '',		//计量单位
          // CCP_TaxPriceMark: '',			//含税价标志
          // CCP_HiddenFlag: '',	//隐藏标志
          // CCP_TaxClassificationCode: '', //税收分类编码
          // CCP_NameOfTaxClassification: '',	//税收分类名称
          // CCP_sp_name: '' // 商品名称
        },
        goodsNumber: false,
        isSelect: false
      }
    },
    created () {
    },
    computed: {
      goods_table () {
        return this.$store.getters.getFormContent(this.formName)
      }
    },
    mounted () {
    },
    methods: {
      add_goods () {
        this.goods_form = this.$options.data().goods_form
        let shu = this.$store.state.form[this.formName][this.$store.state.form[this.formName].length - 1]
        this.goods_form.CCP_bm_code = shu ? shu.CCP_bm_code * 1 + 1 : 0
        layer.open({
          type: 1,
          title: ['商品编码添加', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
          area: ['574px'],
          content: $('#add-goods-content'),   //内容
          resize: false,   //不允许拉伸
          shade: [0.1, '#3b6ea5'],   //遮罩阴影
          cancel: function () {
            document.getElementById('add-goods-content').style.display = 'none'
          }
        })
      },
      add_goods_click () {
        let data = {}
        data.formId = this.formName
        data.content = this.goods_form
        this.$store.commit('addFormInfo', data)
        this.$forceUpdate()
        layer.closeAll()
      },
      cancel_click () {
        layer.closeAll()
      },
      select_goods (i) {
        this.goodsNumber = i
      },
      change_goods () {
        if (!this.goodsNumber && this.goodsNumber != 0) {
          layer.msg('选中一个才能修改哦')
        } else {
          this.goods_form = { ...this.goods_table[this.goodsNumber] }
          layer.open({
            type: 1,
            title: ['商品编码修改', 'color:#000;background-color:#cae1f7;border-bottom:solid 1px #cae1f7;'],
            area: ['574px'],
            content: $('#add-goods-content'),   //内容
            resize: false,   //不允许拉伸
            shade: [0.1, '#3b6ea5'],   //遮罩阴影
            cancel: function () {
              document.getElementById('add-goods-content').style.display = 'none'
            }
          })
        }
      },
      delete_goods () {
        let data = {}
        data.formId = this.formName
        data.infoId = [{ ...this.goods_table[this.goodsNumber] }.infoId]
        this.$store.commit('deleteFormInfo', data)
        layer.msg('删除成功')
      }
    }
  }
</script>

